import React, { useEffect } from "react"
import { FacebookProvider, Page } from "react-facebook"

import Banner from "../../components/utils/Banner"
import ShareButtons from "../../components/utils/ShareButtons"
import Layout from "../../components/Layout"

import { Container, Content, ImageLink, Sidebar } from "./style"
import SEO from "../../components/utils/SEO"
import { IArticles, IBreadCrumbOption } from "../../interfaces"
import { translate } from "../../components/utils/translate"

interface IProps {
	pageContext: {
		article: IArticles
	}
}

function ArticleTemplate({ pageContext: { article } }: IProps) {
	const facebookData = {
		id: "731372470600932",
		url: "https://www.facebook.com/kivalitaconsulting/",
	}

	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "articles",
			name: translate("breadCrumb.article"),
			isActive: true,
		},
		{
			url: "articles",
			name: article.title,
			isActive: false,
		},
	]

	const openLandingPage = () => {
		window.open(
			"https://conteudo.kivalitaconsulting.com/e-book-iso-27001",
			"_blank"
		)
	}

	return (
		<Layout
			breadCrumbList={breadCrumbList}
			translateSlug={article.translateSlug}
		>
			<SEO
				title={article.title}
				article={true}
				image={article.postImage.imageUrl}
			/>
			<Banner titlePage={article.title} />
			<Container className="container">
				<div className="wrapper-image-cover">
					<img
						alt="Article image"
						width="100%"
						src={article.postImage.imageUrl}
					/>
				</div>

				<ShareButtons
					url={`https://www.kivalitaconsulting.com/articles/${article.slug}`}
				/>

				<Content dangerouslySetInnerHTML={{ __html: article.article }} />

				<Sidebar>
					<FacebookProvider appId={facebookData.id}>
						<Page href={facebookData.url} tabs="timeline" />
					</FacebookProvider>
					<ImageLink onClick={openLandingPage}>
						<img
							alt="Download Livro Kivalita"
							width="500px"
							src="https://www.kivalitaconsulting.com/wp-content/E-Book_1080x1080.png"
						/>
					</ImageLink>
				</Sidebar>
			</Container>
		</Layout>
	)
}

export default ArticleTemplate
